import 'uni-pages';wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;import App from './App'


import Vue from 'vue'
Vue.config.productionTip = false
App.mpType = 'app'

import uView from "uview-ui"
Vue.use(uView)
uni.$u.config.unit = 'rpx'

Vue.prototype.$reqPost = function ({url,body,rsv=()=>{},rej=()=>{}}) {
  uni.request({
  	url,
  	method: "POST",
  	header:  {
  		'Content-Type': 'application/json;charset=utf-8'
  	},
  	data: body,
  	success (res) {
  		rsv(res)
  	},
  	fail (err) {
			rej(err)
  	}
	})
}	






var baseUrl = "http://localhost:8082" 

Vue.prototype.$baseUrl = baseUrl

const app = new Vue({
    ...App
})
app.$mount()